:root{--width-main-content: 60vw;--font-family-headers: 'Ubuntu';--font-family-body: 'Open Sans';--font-size-body-normal: 12px;--font-size-body-large: 14px;--font-size-body-Large: 16px;--font-size-body-LARGE: 18px;--color-text-navigation: #ffffff;--color-text-highlight: #852000;--color-navigation-background: #0f4c81;--color-text-main: #0f4c81;--color-text-medium: rgb(15, 76, 129, 0.5);--color-text-reverse: #ffffff;--color-background-main: #ffffff;--color-background-warning: #852000;--color-background-alpha-medium: rgba(15, 76, 129, 0.5);--color-background-alpha-light: rgba(15, 76, 129, 0.25);--color-button-disabled: rgba(15, 76, 129, 0.5);--color-border-main: #0f4c81;--color-shadow-light: rgba(15, 76, 129, 0.25);--shape-shadow-main: 0px 0px 5px 2px;--color-outline-main: #0f4c81;--color-outline-alternative: #852000;--transition-time-main: 0.2s}@media (max-width: 1280px){:root{--width-main-content: 75vw}}@media (max-width: 768px){:root{--width-main-content: 90vw}}*{box-sizing:inherit}html{box-sizing:border-box}*,*::before,*::after{margin:0;padding:0}html{font:12px/1.5 'Open Sans',sans-serif;font:var(--font-size-body-normal)/1.5 var(--font-family-body),sans-serif}@media (min-width: 768px){html{font-size:14px;font-size:var(--font-size-body-large)}}@media (min-width: 1280px){html{font-size:16px;font-size:var(--font-size-body-Large)}}@media (min-width: 1600px){html{font-size:18px;font-size:var(--font-size-body-LARGE)}}body,select,option{font:inherit}h1,h2,h3,h4,h5,h6{font-family:'Ubuntu';font-family:var(--font-family-headers);font-weight:500;font-size:1em;margin-bottom:0.5em}h1{font-size:2.8em}h2{font-size:2.4em}h3{font-size:2.0em}h4{font-size:1.7em}h5{font-size:1.4em}h6{font-size:1.2em}a{text-decoration:none}.site-header a{color:var(--color-navigation-text)}

body .site-container{min-height:100vh}body .site-container header,body .site-container footer{color:var(--color-text-navigation);background-color:var(--color-navigation-background)}body .site-container main{min-height:calc(100vh - 14em);color:var(--color-text-main)}body .site-container .site-header,body .site-container .site-footer,body .site-container .site-main{max-width:var(--width-main-content);margin:0 auto}body .site-container .site-header{margin-bottom:1em}body .site-container .site-footer{margin-top:0.5em}

.site-header{padding:1em 0;display:flex;justify-content:space-between}.site-header .site-logo{display:flex;align-items:center;height:6em}.site-header .site-logo .logo-img{height:100%;margin:1em 1em 1em 0}.site-header .site-logo .logo-text{margin:0;line-height:1}.site-header .nav-header .nav-list{list-style:none;list-style-position:outside;display:flex}.site-header .nav-header .nav-item{line-height:6em;margin:0;padding:0}.site-header .nav-header .fas{font-size:2em}.site-header .nav-header .fa-shopping-cart{position:relative;font-size:2em}.site-header .nav-header .cart-size{position:absolute;padding:0.2em 0.5em;border-radius:50%;background:var(--color-text-highlight);font-size:0.5em;font-family:var(--font-family-headers);top:-0.5em;right:-0.5em}

.site-footer{height:6em}

.product-list-container{display:flex;flex-wrap:wrap;align-items:center}h1{width:65%}@media (max-width: 1024px){h1{width:50%}}@media (max-width: 768px){h1{width:100%}}.sort-wrapper{margin-bottom:1.25em;margin-left:auto;display:flex;align-items:center;width:32%;position:relative}@media (max-width: 1024px){.sort-wrapper{width:50%}}@media (max-width: 768px){.sort-wrapper{width:100%}}.sort-wrapper .sort-label{line-height:2.8em;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.sort-wrapper .sort-options,.sort-wrapper option,.sort-wrapper .arrow{color:var(--color-text-highlight);font-weight:bold}.sort-wrapper .sort-options{-webkit-appearance:none;-moz-appearance:none;appearance:none;line-height:2.8em;flex-grow:1;margin-left:1em;padding:0.5rem;border:none;outline:none;background-color:var(--color-background-alpha-light)}.sort-wrapper .sort-options:hover,.sort-wrapper .sort-options:hover+.arrow{color:var(--color-text-main)}.sort-wrapper .sort-options option{padding:1em}.sort-wrapper .arrow{padding-right:0.2em;position:absolute;font-size:2em;z-index:-1;right:0;top:0}.product-list{display:flex;justify-content:space-between;flex-wrap:wrap}.product-list::after{content:"";width:32%}.product-list .list-title{width:100%;font-weight:bold;margin-bottom:0.5em}

