.product-details{display:flex;flex-wrap:wrap;justify-content:space-between}@media (max-width: 600px){.product-details{display:block}}.product-details .manufacturer{flex-basis:100%;margin-bottom:1em}.product-details .images{flex:0 0 30%}.product-details .images img{width:100%;height:auto}.product-details .image-main img{box-shadow:var(--shape-shadow-main) var(--color-shadow-light)}.product-details .image-thumbs{display:flex;flex-wrap:wrap}.product-details .image-thumbs .image-label{width:23.5%;margin:1em 0;margin-right:2%}.product-details .image-thumbs .image-label:nth-child(4n){margin-right:0}.product-details .image-thumbs .image-radio{display:none}.product-details .image-thumbs .image-placeholder{padding-top:0.2rem;overflow:hidden}.product-details .image-thumbs .image-placeholder:hover{outline:2px solid var(--color-outline-alternative);cursor:pointer}.product-details .image-thumbs .image-radio:checked+.image-placeholder{outline:2px solid var(--color-outline-main)}.product-details .image-thumb img{width:100%;-webkit-transition:-webkit-transform .2s ease;transition:-webkit-transform .2s ease;transition:transform .2s ease;transition:transform .2s ease, -webkit-transform .2s ease}.product-details .image-thumb:hover img{-webkit-transform:scale(1.1);transform:scale(1.1)}.product-details .wrapper{flex:0 0 65%;display:flex;flex-direction:column}.product-details .info-wrapper h4{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;padding:0.5rem 1rem;color:var(--color-text-reverse);background-color:var(--color-background-warning)}.product-details .info-wrapper h6{margin-top:2em}.product-details .qty-wrapper{margin-left:auto;display:block}.product-details .qty-wrapper .form-button{margin:0 auto}.product-details .qty-wrapper .cart{margin-top:1em}

.size-list{padding:1em 0;display:flex;flex-wrap:wrap;justify-content:space-between}.size-list .size-list-title{flex-basis:100%;margin-top:1em}.size-list .label-list{display:flex;width:100%}.size-list .size-label{flex-basis:10%;padding:1em;margin-right:2%;outline:2px solid var(--color-outline-main);text-align:center}.size-list .size-label:last-child{margin-right:auto}.size-list .size-label .size-radio{display:none}.size-list .size-label .size-item{font-weight:bold}.size-list .size-label:hover{color:#f0f0f0;background:var(--color-text-medium);cursor:pointer}.size-list .size-label.selected{color:#f0f0f0;background:var(--color-text-main)}.size-list #size-warning{visibility:hidden;padding:0.5em 1em;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.size-list #size-warning.visible{visibility:visible;font-weight:bold;color:var(--color-text-reverse);background:var(--color-background-warning)}

.form-button{display:block;padding:0.6em;outline:none;border:2px solid var(--color-border-main);color:var(--color-text-main);background-color:var(--color-background-main);font-size:1em;font-weight:bold;-webkit-transition:color var(--transition-time-main),background-color var(--transition-time-main);transition:color var(--transition-time-main),background-color var(--transition-time-main)}.form-button:hover{color:var(--color-background-main);background-color:var(--color-text-main);cursor:pointer}.form-button.product-table{font-size:1.5em;padding:0;border:none;background:none;-webkit-transition:color var(--transition-time-main);transition:color var(--transition-time-main)}.form-button.product-table:hover{color:var(--color-text-highlight)}.form-button.product-table:disabled{color:var(--color-button-disabled)}.form-button.product-table:disabled:hover{cursor:default}

.qty-wrapper{display:flex;align-items:baseline}.qty-wrapper .product-item-qty{display:flex;align-items:baseline;text-align:center}.qty-wrapper .qty-number{width:1.5em;font-size:1.5em;font-weight:bold;text-align:center;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}

.cart-wrapper{width:var(--width-main-content)}.cart-wrapper h1{font-weight:bold}.cart-wrapper a{color:var(--color-text-highlight)}.cart-wrapper a:hover{text-decoration:underline}.total-price-wrapper{margin-left:auto;width:40%}.total-price-wrapper h4{display:flex;justify-content:space-between}.total-price-wrapper .text,.total-price-wrapper .price{font-weight:bold}.total-price-wrapper .form-button{width:100%}

.product-list-table{list-style:none;padding:0;padding-bottom:2em}.product-list-table .cart-image img{width:6em}.product-list-table .product-item{display:flex;flex-wrap:wrap;justify-content:space-between;align-items:center;text-align:center;margin-bottom:1em;padding-bottom:1em;border-bottom:2px solid var(--color-outline-alternative)}.product-list-table .product-item.header{font-weight:bold;color:var(--color-navigation-text);background:var(--color-donut-border-dark)}.product-list-table .product-item button{display:inline}.product-list-table .product-item-sprice,.product-list-table .product-item-qty,.product-list-table .product-item-tprice{width:14%}.product-list-table .product-item-title{text-align:left;width:50%}.product-list-table .product-item-title a{display:flex;align-items:center;color:var(--color-text-main)}.product-list-table .product-item-title a:hover{text-decoration:none;color:var(--color-text-highlight)}.product-list-table .product-item-title .title-info{display:flex;flex-direction:column}.product-list-table .product-item-title .title-info .manufacturer,.product-list-table .product-item-title .title-info .name,.product-list-table .product-item-title .title-info .size{font-weight:bold}.product-list-table .product-item-title .title-info .name{font-size:1.2em}.product-list-table .product-item-title .button-delete{font-size:0.8em;padding-left:6rem}.product-list-table .product-item-title .button-delete i{margin-right:0.5em}.product-list-table .product-item-tprice{font-weight:bold;font-size:1.2em}@media (max-width: 1280px){.product-list-table .product-item-title{width:40%}.product-list-table .product-item-sprice,.product-list-table .product-item-qty,.product-list-table .product-item-tprice{width:18%;padding-top:1em}}@media (max-width: 768px){.product-list-table .product-item.header{display:none}.product-list-table .product-item-title{width:100%}.product-list-table .product-item-sprice,.product-list-table .product-item-qty,.product-list-table .product-item-tprice{width:30%;padding-top:1em}}.product-list-table .product-item-qty button{margin:0.5em}.product-list-table .product-item-qty span{display:inline-block;width:1em;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.product-list-table .product-item-qty .qty-number{font-size:1.5em;font-weight:bold}

